import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable, from } from 'rxjs';
import { map } from 'rxjs/operators';

import { User } from '../../models/user';
import { environment } from '../../../environments/environment';

@Injectable({
	providedIn: 'root'
})
export class AuthenticationService {
	private currentUserSubject: BehaviorSubject<User>;
	public currentUser: Observable<User>;
  
	private url: string = environment.api.backend.url;

	constructor(private http: HttpClient) {
		this.currentUserSubject = new BehaviorSubject<User>(JSON.parse(localStorage.getItem('vtr_current_user')));
		this.currentUser = this.currentUserSubject.asObservable();
	}

	public get currentUserValue(): User {
		return this.currentUserSubject.value;
	}

	login(username: string, password: string) {
		return this.http.post<any>(`${this.url}/auth/login`, {
				email: username,
				password:password
			})
			.pipe(map(user => {
				// login successful if there's a jwt token in the response
				if (user && user.token) {
					// store user details and jwt token in local storage to keep user logged in between page refreshes
					localStorage.setItem('vtr_current_user', JSON.stringify(user));
					this.currentUserSubject.next(user);
				}

				return user;
			}));
	}

	logout() {
		// remove user from local storage to log user out
		localStorage.removeItem('vtr_current_user');
		this.currentUserSubject.next(null);
	}
}
