import { Component, OnInit, Input } from '@angular/core';
import { DateFormatPipe } from 'src/app/pipe/date-format.pipe';

@Component({
  selector: 'app-list-experience',
  templateUrl: './list-experience.component.html',
  styleUrls: ['./list-experience.component.scss']
})
export class ListExperienceComponent implements OnInit {
  @Input() experience: any;
  constructor(private datePipe: DateFormatPipe) { }

  ngOnInit() {
  }

  parseDate(date: Date){
		return this.datePipe.transform(date, "MM/yyyy");
	}
}
