import { Injectable } from '@angular/core';
import { HttpClient, HttpParams} from '@angular/common/http';
import { HttpParamsOptions } from '@angular/common/http/src/params';
import { environment } from '../../../environments/environment';


@Injectable({
	providedIn: 'root'
})
export class ApiBackendService {
	private url: string = environment.api.backend.url;

	constructor(private http: HttpClient) { }

	get(route: string, params: any) {
		const _params = new HttpParams({fromObject: params});
		return this.http.get<any>(`${this.url}/${route}`, { params: _params });
	}
	post(route: string, obj: object) {
		return this.http.post<any>(`${this.url}/${route}`, obj);
	}

}
