import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { ApiBackendService } from 'src/app/admin/services/api-backend.service'

@Component({
	selector: 'app-contact',
	templateUrl: './contact.component.html',
	styleUrls: ['./contact.component.scss']
})
export class ContactComponent implements OnInit {
	submited: boolean = false;
	submited_error: boolean = false;
	contactForm = new FormGroup({
		name: new FormControl('', Validators.required),
		email: new FormControl('', [Validators.required, Validators.email]),
		description: new FormControl('', Validators.required),
	});
	constructor(private apiBackend: ApiBackendService) { }

	ngOnInit() {
	}

	onSubmit() {
		// TODO: Use EventEmitter with form value
		console.warn(this.contactForm);
		let body = {
			url: window.location.host, 
			...this.contactForm.value
		};
		this.apiBackend.post("email/contact", body).subscribe( data => {
			this.submited = true;
			this.submited_error = false;
		}, err => { 
			console.error("Erro: ", err) 
			this.submited_error = true;
		})
	}
}
