import { Component, OnInit } from '@angular/core';
import { ApiBackendService } from '../../admin/services/api-backend.service';
import { Timeline } from '../../models/timeline';
import { Skill } from '../../models/skill';

export class Experience {
	id: any;
	title: string;
	description: string;
	image: string;
	role_of: string;
	background: string;
	date_start: Date;
	date_end: Date;
	active: boolean;
}

@Component({
	selector: 'app-home',
	templateUrl: './home.component.html',
	styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit {
	projetos: any;
	skills: any;
	experience: Experience[];

	constructor(private apiBackend: ApiBackendService) {
		this.projetos = new Array();
		this.skills = new Array();	
		this.experience = new Array();	
	}

	ngOnInit() {
		this.apiBackend.get("skills",{"_order":"asc", "_sort":"title"}).subscribe( skills => {
			this.skills = skills.map( skill => {
				let _skill = new Skill();
				_skill.title = skill.title;
				_skill.percent = skill.percent || false;
				_skill.active = skill.active;
				
				return _skill;
			});
		})
		this.apiBackend.get("experiences",{"_order":"desc", "_sort":"date_end"}).subscribe( experience => {
			this.experience = experience;
		})
		this.apiBackend.get("projects",{"_order":"desc", "_sort":"date_end"}).subscribe( projetos => {
			this.projetos = projetos.map( projeto => {
				let timeline = new Timeline();
				timeline.title = projeto.title || null;
				timeline.subtitle = projeto.company || null;
				timeline.date = projeto.date_end;
				timeline.description = projeto.description;
				timeline.active = projeto.active;
				return timeline;
			});
		})
	}

	scroll(el: HTMLElement) {
		console.warn( "el: ", el )
		el.scrollIntoView({behavior:"smooth"});
	}

}
